import React from "react";
//
import Slider from "../slider/index";
import '../../styles/slider.css'
import "../../styles/styles.css";
import "../../styles/conCate.css";

/**
 * Componente que retorna los videos ordenados en un slider dependiendo de su categoria.
 * @param {*} param0 
 * @returns Slider con los videos.
 */
const VideosCategoriaFila = ({ categoria, verVideo, ...props }) => {
  if (categoria) {
    const videosEnCategoria = props.videos && props.search(props.videos).filter((video) => (
      video.categorias.some((element) => categoria.categoria === element.categoria)
    ));

    return (
      <div>
        {videosEnCategoria.length > 0 && (
          <div className="">
            <h2 className="titleCateg ms-3" key={categoria.id}>| {categoria.categoria}<small > ‚ñ∂</small></h2>
            <div>
              <Slider verVideo={verVideo}>
                {videosEnCategoria.map((video) => (
                  <Slider.Item
                    key={video.id}
                    video={video}
                    histUsers={props.histUsers}
                    user={props.user}
                    verVideo={verVideo}
                  >
                  </Slider.Item>
                ))}
              </Slider>
            </div>
          </div>
        )}
      </div>
    );
  }
};

export default VideosCategoriaFila;
